import React from "react"
import DocLayout from "../../components/docs/layout/doclayout"
import QnA from "../../components/docs/qna/qna"
import * as styles from "../../components/docs/qna/qna.module.css"
import ShellDecorator from "../../components/common/shelldecorator/shelldecorator"
import Codeblock from "../../components/common/codeblock/codeblock"
import { Image } from "react-bootstrap"
import Seo from "../../components/seo";
import {useI18next} from "gatsby-plugin-react-i18next";

const Faq = () => {
    const { language } = useI18next()
    const pageTitle = "FAQ"

    return (
      <>
        <Seo lang={language} title="Frequently Asked Questions" description="Answers to the most frequently asked questions about DockoVPN free open source self-hosted server." />
        <DocLayout section={ pageTitle }>
            <p>Here we collected the most frequently asked question from our users and prepared answers to them. We aim to update the page quite often. So, if you didn't find the answer to your question, make sure to come check this page later. Or, you can ask you question directly in our support chat via Facebook messaeger widget on the main page.</p>
            <ul className={ styles.fqa }>
                <li>
                    <QnA question="How much does your product cost?">
                        Our open-source VPN server is absolutely free to use.
                    </QnA>
                </li>
                <li>
                    <QnA question="What do I do if can't download config?">
                        If your DockoVPN server is running, but you can't download config, then most-likely you have Firewall settings that prevent you from downloading client config via port DockoVPN uses for serving configs.
                        To fix that, try to disable Firewall, for istance, on Ubuntu you can run:
                        <br />
                        <br />
                        <ShellDecorator>
                            <Codeblock language="shell">
{`ufw disable`}
                            </Codeblock>
                        </ShellDecorator>
                        <br />
                        If by some reason disabling Firewall isn't an option for you, you can try to use different config port.
                    </QnA>
                </li>
                <li>
                    <QnA question="Is it possible to change default DockoVPN ports?">
                        You can change default ports DockoVPN serer uses. To do so, use following snippet:
                        <br />
                        <br />
                        <ShellDecorator>
                            <Codeblock language="shell">
{`DOCKOVPN_CONFIG_PORT=<custom config port>
DOCKOVPN_TUNNEL_PORT=<custom vpn port>
docker run -it --rm --cap-add=NET_ADMIN \\
-p $DOCKOVPN_TUNNEL_PORT:1194/udp \\
-p $DOCKOVPN_CONFIG_PORT:8080/tcp \\
-e HOST_ADDR=$(curl -s https://api.ipify.org) \\
-e HOST_CONF_PORT="$DOCKOVPN_CONFIG_PORT" \\
-e HOST_TUN_PORT="$DOCKOVPN_TUNNEL_PORT" \\
--name dockovpn alekslitvinenk/openvpn`}
                            </Codeblock>
                        </ShellDecorator>
                        <br />
                        Make sure to replace <strong>{`<custom config port>`}</strong> and <strong>{`<custom vpn port>`}</strong> with real port numbers you need.
                    </QnA>
                </li>
                <li>
                    <QnA question="How do I create a new client config?">
                        In case you need to create a new client config config, you can use snippet below:
                        <br />
                        <br />
                        <ShellDecorator>
                            <Codeblock language="shell">
{`docker exec dockovpn ./genclient.sh z `}
                            </Codeblock>
                        </ShellDecorator>
                        <br />
                        The code above will generate a new client configuration, package it into a zip archive and serve it via a one-shot web-server.
                    </QnA>
                </li>
                <li>
                    <QnA question="How do I revoke client config?">
                        To revoke client config you need to know client Id of the config you want to revoke. You can find it in the config itself:
                        <figure>
                            <Image src="/images/client-config-id.png" width="700" height="auto" alt="Client configuration file with client Id"/>
                            <figcaption>Pic 1. Client configuration file with client Id</figcaption>
                        </figure>
                        Once client Id is know, you can run the following command:
                        <br />
                        <br />
                        <ShellDecorator>
                            <Codeblock language="shell">
{`docker exec dockovpn ./rmclient.sh <client id>`}
                            </Codeblock>
                        </ShellDecorator>
                        <br />
                        Make sure to replace <strong>{`<client id>`}</strong> with real client Id you intend to revoke.
                    </QnA>
                </li>
                <li>
                    <QnA question="How do I make DockoVPN bind to a different network interface?">
                        By default DockoVPN binds to <strong>eth0</strong> network interface, but you can overide this behaviour by supplying <strong>NET_ADAPTER</strong> environment variable to container. Se snippet below:
                        <br />
                        <br />
                        <ShellDecorator>
                            <Codeblock language="shell">
{`docker run -it --rm --cap-add=NET_ADMIN \\
-p 1194:1194/udp -p 80:8080/tcp \\
-e HOST_ADDR=$(curl -s https://api.ipify.org) \\
-e NET_ADAPTER=<network interface> \\
--name dockovpn alekslitvinenk/openvpn`}
                            </Codeblock>
                        </ShellDecorator>
                        <br />
                        Make sure to replace <strong>{`<network interface>`}</strong> with real network interface you intend to use.
                    </QnA>
                </li>
                <li>
                    <QnA question="Which VPN protocols do you support?">
                        At the moment we support only OpenVPN protocol. We plan to add support to <strong>Wireguard</strong> and <strong>IKEv2</strong> soon as well.
                    </QnA>
                </li>
            </ul>
        </DocLayout>
      </>
    )
}

export default Faq