import React, { useState } from "react"
import * as styles from "./qna.module.css"
import { BsFillCaretDownSquareFill, BsFillCaretRightSquareFill } from 'react-icons/bs'

const QnA = ({question, children}) => {
    const [isOpen, setOpen] = useState(false)
    const qnaStyle = styles.qna + ""
    const qnaStyleOpen = isOpen ? " " + styles.open : ""
    const qnaComputed = qnaStyle + qnaStyleOpen

    const linkClickHandler = (event) => {
        event.preventDefault();

        setOpen(!isOpen)
    }

    return (
        <div className={ qnaComputed }>
            <div className={ styles.qnaQWrap }>
                <a href="#" onClick={linkClickHandler}><span className={ styles.qnaQ }>{ question }</span></a>&nbsp;
                <div className={ styles.arrowWrap }>
                    <div className={ styles.rightArrow } onClick={ () => setOpen(true) }>
                        <BsFillCaretRightSquareFill />
                        </div>
                    <div className={ styles.downArrow } onClick={ () => setOpen(false) }>
                        <BsFillCaretDownSquareFill />
                    </div>
                </div>
            </div>
            <div className={ styles.qnaA }>
                { children }
            </div>
        </div>
    )
}

export default QnA